export default {
	 name:'pfSwitch',
	 props: {
		   msg: {
                type: Boolean,
                default: true
            },
		    Styles: {
			     type:String,
			     default:"red" //red、black、green、blue、grey  可选择颜色
			 },
		},
		data() {
			return {
			  startX:0,
			  moveX:0,
			  isChecked:this.msg,
			  isTouch:false,
			}
		},
		onLoad(e) {

		},
		created() {
	    
	    },
		methods: {
	        touchstart(event) {
                this.startX = event.touches[0].pageX;
				this.isTouch=true
            },
            touchmove(event) {
                this.moveX = event.touches[0].pageX - this.startX;
				this.isTouch=false;
            },
            touchend(event) {
				if(this.isTouch) return; //滑动才能往下执行
                if (44 / 3 < Math.abs(this.moveX)) {
                    if (this.moveX < 0) {
                        this.isChecked = false;
                    } else {
                        this.isChecked = true;
                    }
                }
                this.moveX = 0;
                this.startX = 0;
				this.$emit("getChecked",this.isChecked);
            },
			onClick(event){
                if(!this.isTouch) return; //不是滑动才能执行
				if (this.isChecked) {
				    this.isChecked = false;
				} else {
				    this.isChecked = true;
				}
				this.$emit("getChecked",this.isChecked);
			}
		},
		computed: {
			
        }
	}
